.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ZipCodeSearchTitle{
  background-color: #282c34;
  padding: 2rem;

}

.ZipCodeTitle{
  font-size: 2.5rem;
  color: white;
}



.zipCodeInput{
  border: 1px solid #ccc;
  border-radius: 4px;
  margin: 15px 0;
  padding: 12px 25px;
}

.searchButton{
  margin: 10px 0;
  padding: 0.5rem;

}


.informtation{
  border: 1px solid #ccc;
  width: 40%;
  padding: 2rem;
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
  margin-left: 24rem;
}